@import "./_functions.scss";
@import "./_colour.scss";
@import "./_font.scss";

// Use for space within components.
$c-spacing-2xs: .25rem;
$c-spacing-xs: .5rem;
$c-spacing-s: .75rem;
$c-spacing-m: 1rem;
$c-spacing-l: 1.5rem;
$c-spacing-xl: 2rem;
$c-spacing-2xl: 2.5rem;

// Use for space between components.
$l-spacing-2xs: 1rem;
$l-spacing-xs: 1.5rem;
$l-spacing-s: 2rem;
$l-spacing-m: 3rem;
$l-spacing-l: 4rem;
$l-spacing-xl: 6rem;
$l-spacing-2xl: 8rem;

$font-size-s: .75rem;
$font-size-default: 1rem;
$font-size-m: 1.25rem;
$font-size-l: 1.5rem;

$animation-timing: cubic-bezier(.4, 0, 0, 1);
$animation-fast   : .2s;
$animation-normal : .3s;
$animation-slow   : .8s;

$border-radius-zero   : 0;
$border-radius-xs     : 2px;
$border-radius-s      : 4px;
$border-radius-m      : 6px;
$border-radius-l      : 8px;
$border-radius-xl     : 40px;
$border-radius-2xl     : 50%;

$border-thickness-zero        : 0;
$border-thickness-default     : 1px;
$border-thickness-double      : 2px;
$border-thickness-focus       : 4px;
$border-thickness-focus-rem   : .25rem;
$border-thickness-default-rem : .0625rem; // for calculations with rem values
$border-thickness-double-rem  : .125rem; // for calculations with rem values

$breakpoints-small    : px2rem(320); // 320px xsmall
$breakpoints-medium   : px2rem(576); // 576px small
$breakpoints-large    : px2rem(768); // 768px large
$breakpoints-xlarge   : px2rem(992); // 992px xlarge
$breakpoints-2xlarge   : px2rem(1200); // 1200px 2xlarge

$grid-margin-xs: $l-spacing-2xs;
$grid-margin-s: $l-spacing-2xs/2;
$grid-margin-l: $l-spacing-xs/2;
$grid-margin-2xl: $l-spacing-s/2;

$grid-gutter-s: $grid-margin-s;
$grid-gutter-m: $grid-gutter-s;
$grid-gutter-l: $grid-margin-l;
$grid-gutter-xl: $grid-gutter-l;
$grid-gutter-2xl: $grid-margin-2xl;
$grid-max-width: 90rem;
