@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./_definition-list.scss";
@import "./_mobile-scroll.scss";

table {
  width: 100%;
  max-width: 100%;
  margin-bottom: $c-spacing-xs;
  background-color: transparent;
  border-collapse: collapse;
  border-spacing: 0;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;

  th {
    color: $colour-neutral-grey-900;
  }

  thead {
    display: none;
  }

  thead th {
    border-bottom: $border-thickness-default solid $colour-neutral-grey-800;
  }

  td,
  th {
    padding-top: $c-spacing-2xs;
    padding-bottom: $c-spacing-2xs;

    &[data-title]:before {
      content: attr(data-title);
      display: block;
      font-weight: bold;
      width: 50%;
      padding-right: $c-spacing-m;
      flex-shrink: 0;
    }

    display: inline-block;
    display: flex;
    width: 100%;
  }

  tr {
    overflow-x: auto;
    display: block;
  }
}

.table {
  tr {
    th:first-child,
    td:first-child {
      padding-top: $c-spacing-xs;
    }

    th:last-child,
    td:last-child {
      padding-bottom: $c-spacing-xs;
    }

    &:not(:last-child) {
      border-bottom: $border-thickness-default solid $colour-neutral-grey-300;
    }
  }
}

.table--striped {
  tr:nth-child(even) {
    background: $colour-neutral-grey-100;
  }

  tbody tr:not(:last-child) {
    border-bottom: none;
  }

  th,
  td {
    padding-left: $c-spacing-xs;
    padding-right: $c-spacing-xs;
  }
}

@media screen and (min-width: $breakpoints-medium) {
  table {
    display: table;

    td,
    th {
      display: table-cell;
      width: initial;
      padding-left: $l-spacing-2xs;
      padding-right: $l-spacing-2xs;

      &[data-title]:before {
        content: "";
        display: none;
      }
    }

    thead {
      display: table-header-group;
    }

    tbody {
      display: table-row-group;

      tr:not(:last-child) {
        border-bottom: none;
      }
    }

    tr {
      display: table-row;
    }
  }

  .table {
    tr {
      &:not(:last-child) {
        border-bottom: none;
      }
    }

    tr td {
      padding-top: $c-spacing-xs;
      padding-bottom: $c-spacing-xs;
    }
  }

  .table--striped {
    th,
    td {
      padding: $c-spacing-xs;
    }
  }
}
