.ikea-container {
  @include ikeacontainer();
}

.ikea-row {
  @include ikearow();
}

.ikea-col {
  @include ikeaautocol();

  .ikea-row {
    @include ikeanestedrow();
  }
}

[class*="ikea-col"] {
  .ikea-row {
    @include ikeanestedrow();
  }
}

@for $i from 1 through 12 {
  .ikea-col-#{$i},
  .ikea-col-sm-#{$i},
  .ikea-col-md-#{$i},
  .ikea-col-lg-#{$i},
  .ikea-col-xl-#{$i},
  .ikea-col-2xl-#{$i} {
    @include ikeagetcolDefault();
    margin-bottom: $c-spacing-s;
  }

  @media all and (min-width: $breakpoints-small) {
    .ikea-col-#{$i} {
      @include ikeagetcol(12, $i, "s");
    }
  }

  @media all and (min-width: $breakpoints-large) {
    .ikea-col-#{$i} {
      @include ikeagetcol(12, $i, "l");
    }
  }

  @media all and (min-width: $breakpoints-2xlarge) {
    .ikea-col-#{$i} {
      @include ikeagetcol(12, $i, "2xl");
    }
  }
}

.ikea-break {
  @include ikeagetcolDefault(1,1);
  padding: 0 $c-spacing-xs;

  &--line {
    @include ikeagetcolDefault(1,1);
    padding: 0 $c-spacing-xs;
    border-top: $border-thickness-default solid $divider-colour-horizontal;
  }
}

@for $i from 1 through 12 {
  .ikea-offset-#{$i} {
    @include ikeaoffset(12, $i);
  }
}

