
@mixin btn-pill-outlined($colour-default: $colour-neutral-grey-900,
$colour-hover: $colour-neutral-grey-900,
$colour-pressed: $colour-neutral-grey-800,
$colour-checked: $colour-neutral-grey-900,
$text-default: $colour-neutral-grey-900,
$text-hover: $colour-neutral-white,
$text-pressed: $colour-neutral-white,
$text-checked: $colour-neutral-white) {
  @include btn-pill-outlined-idle($colour-default);

  &:hover:not(:disabled),
  &:focus:not(:disabled) {
    @include btn-pill-outlined-hover($colour-hover, $text-hover);
  }

  &:active:not(:disabled) {
    @include btn-pill-outlined-pressed($colour-pressed, $text-pressed);
  }

  &:disabled {
    opacity: .4;
  }
}

@mixin btn-pill-outlined-idle($colour-default: $colour-neutral-grey-900) {
  text-align: center;
  display: inline-block;
  text-decoration: none;
  font-weight: bold;
  margin-bottom: 0;
  font-size: $body-font-size-xs;
  line-height: 1.4;

  @include btn-state(transparent, $colour-default, $c-spacing-2xl, #{$c-spacing-xs - .125rem} $c-spacing-l, $border-thickness-double solid $colour-default);
}

@mixin btn-pill-outlined-hover($colour-hover: $colour-neutral-grey-900, $text-hover: $colour-neutral-grey-900) {
  @include btn-state($colour-hover, $text-hover);
  border-color: $colour-hover;
}

@mixin btn-pill-outlined-pressed($colour-pressed: $colour-neutral-grey-900, $text-pressed: $colour-neutral-white) {
  @include btn-state($colour-pressed, $text-pressed);
  border-color: $colour-pressed;
}

@mixin btn-pill-outlined-checked($colour-checked: $colour-neutral-grey-900, $text-checked: $colour-neutral-white) {
  @include btn-state($colour-checked, $text-checked);
  border-color: $colour-checked;

  .pill-icon-close {
    display: inline-block;
  }
}

@mixin btn-pill-outlined-checked-icon-left() {
  padding-left: $c-spacing-xs;

  .pill-icon-close {
    padding-right: $c-spacing-xs;
  }
}

@mixin btn-pill-outlined-checked-icon-right() {
  padding-right: $c-spacing-xs;

  .pill-icon-close  {
    padding-left: $c-spacing-xs;
  }
}
