@import "./node_modules/@ikea-ux/animations/style.scss";
@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/button/lib/mixins.scss";
@import "./lib/_mixins.scss";

.pill-wrap {
  display: inline-block;
  margin-right: $c-spacing-xs;
  margin-bottom: $c-spacing-xs;

  &:last-child {
    margin-right: 0;
  }

  [type="radio"]:checked,
  [type="checkbox"]:checked {
    + .pill {
      @include btn-pill-outlined-checked();
    }
  }

  [type="radio"]:checked + .pill__choice {
    @include btn-pill-outlined-checked(
      $colour-brand-blue,
      $colour-neutral-white
    );
  }

  [type="checkbox"]:checked + .pill__multiselect {
    @include btn-pill-outlined-checked-icon-left();
  }

  [type="radio"]:checked + .pill__singleselect {
    @include btn-pill-outlined-checked-icon-right();
  }

  .pill {
    position: relative;
    cursor: pointer;
    vertical-align: middle;
    @include btn-pill-outlined();

    &-copy {
      display: flex;
      align-items: center;
    }

    &-icon-close {
      display: none;
    }

    .svg-icon {
      display: block;
    }
  }
}
