@mixin btn-text(
  $bg-default: transparent,
  $bg-hover: $colour-neutral-grey-200,
  $bg-pressed: $colour-neutral-grey-300,
  $text-default: $colour-neutral-grey-900,
  $text-hover: $colour-neutral-grey-800
) {
  @include btn-text-idle($bg-default, $text-default);

  &:focus:not(:disabled),
  &:hover:not(:disabled) {
    @include btn-text-hover($bg-hover, $text-hover);
  }

  &:active:not(:disabled) {
    @include btn-text-pressed($bg-pressed);
  }
}

@mixin btn-text-idle($bg-default: transparent, $text-default: $colour-neutral-grey-900) {
  @include btn-state($bg-default, $text-default, "", $c-spacing-xs $c-spacing-l);
}

@mixin btn-text-hover($bg-hover: $colour-neutral-grey-200,  $text-hover: $colour-neutral-grey-800) {
  @include btn-state($bg-hover, $text-hover, $border-radius-s);
}

@mixin btn-text-pressed($bg-pressed: $colour-neutral-grey-300) {
  @include btn-state($bg-pressed, "", $border-radius-l);
}
