@import "./node_modules/@ikea-ux/variables/style.scss";

.loading-spinner {
  position: relative;
  display: block;
  margin: 10px;
  font-size: 4px;
  width: 1em;
  height: 1em;
  border-radius: $border-radius-2xl;
  text-indent: -9999em;
  // animation: load-dark 1.1s infinite ease;
  animation-duration: 1.1s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  transform: translateZ(0);

  &--light {
    animation-name: load-light;
  }

  &--dark {
    animation-name: load-dark;
  }

  &__label {
    display: inline-block;
    text-indent: -9999px;
  }
}

@keyframes load-dark {
  0%,
  100% {
    box-shadow: 0 -2.6em 0 0 #333333, 1.8em -1.8em 0 0 rgba(0, 0, 0, .2), 2.5em 0 0 0 rgba(0, 0, 0, .2), 1.75em 1.75em 0 0 rgba(0, 0, 0, .2), 0 2.5em 0 0 rgba(0, 0, 0, .2), -1.8em 1.8em 0 0 rgba(0, 0, 0, .2), -2.6em 0 0 0 rgba(0, 0, 0, .5), -1.8em -1.8em 0 0 rgba(0, 0, 0, .7);
  }

  12.5% {
    box-shadow: 0 -2.6em 0 0 rgba(0, 0, 0, .7), 1.8em -1.8em 0 0 #333333, 2.5em 0 0 0 rgba(0, 0, 0, .2), 1.75em 1.75em 0 0 rgba(0, 0, 0, .2), 0 2.5em 0 0 rgba(0, 0, 0, .2), -1.8em 1.8em 0 0 rgba(0, 0, 0, .2), -2.6em 0 0 0 rgba(0, 0, 0, .2), -1.8em -1.8em 0 0 rgba(0, 0, 0, .5);
  }

  25% {
    box-shadow: 0 -2.6em 0 0 rgba(0, 0, 0, .5), 1.8em -1.8em 0 0 rgba(0, 0, 0, .7), 2.5em 0 0 0 #333333, 1.75em 1.75em 0 0 rgba(0, 0, 0, .2), 0 2.5em 0 0 rgba(0, 0, 0, .2), -1.8em 1.8em 0 0 rgba(0, 0, 0, .2), -2.6em 0 0 0 rgba(0, 0, 0, .2), -1.8em -1.8em 0 0 rgba(0, 0, 0, .2);
  }

  37.5% {
    box-shadow: 0 -2.6em 0 0 rgba(0, 0, 0, .2), 1.8em -1.8em 0 0 rgba(0, 0, 0, .5), 2.5em 0 0 0 rgba(0, 0, 0, .7), 1.75em 1.75em 0 0 #333333, 0 2.5em 0 0 rgba(0, 0, 0, .2), -1.8em 1.8em 0 0 rgba(0, 0, 0, .2), -2.6em 0 0 0 rgba(0, 0, 0, .2), -1.8em -1.8em 0 0 rgba(0, 0, 0, .2);
  }

  50% {
    box-shadow: 0 -2.6em 0 0 rgba(0, 0, 0, .2), 1.8em -1.8em 0 0 rgba(0, 0, 0, .2), 2.5em 0 0 0 rgba(0, 0, 0, .5), 1.75em 1.75em 0 0 rgba(0, 0, 0, .7), 0 2.5em 0 0 #333333, -1.8em 1.8em 0 0 rgba(0, 0, 0, .2), -2.6em 0 0 0 rgba(0, 0, 0, .2), -1.8em -1.8em 0 0 rgba(0, 0, 0, .2);
