@mixin pulse($name) {
  @keyframes #{$name} {
    from {
      transform: scale3d(1.5, 1.5, 1);
    }

    to {
      transform: scale3d(1.7, 1.7, 1);
    }
  }
}

@mixin rotate($name) {
  @keyframes #{$name} {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(359deg);
    }
  }
}

@mixin fade-in-out($name) {
  @keyframes #{$name} {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }
}

@mixin delayed-fade-in($name) {
  @keyframes #{$name} {
    0% {
      opacity: 0;
    }

    50% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }
}

@mixin fade-out-in($name) {
  @keyframes #{$name} {
    0% {
      opacity: 1;
    }

    100% {
      opacity: 0;
    }
  }
}

@mixin fade-in-up($name) {
  @keyframes #{$name} {
    from {
      opacity: 0;
      transform: translate3d(0, 100%, 0);
    }

    to {
      opacity: 1;
      transform: none;
    }
  }
}

@mixin zoom-enter($name) {
  @keyframes #{$name} {
    from {
      transform: scale3d(.3, .3, .3);
    }
  }
}

@mixin zoom-leave($name) {
  @keyframes #{$name} {
    to {
      transform: scale3d(.3, .3, .3);
    }
  }
}

@mixin shake($name) {
  @keyframes #{$name} {
    from,
    to {
      transform: translate3d(0, 0, 0);
    }

    10%,
    50% {
      transform: translate3d(0, 0, 0);
    }

    60% {
      transform: translate3d(3px, 0, 0);
    }

    70% {
      transform: translate3d(-3px, 0, 0);
    }

    80% {
      transform: translate3d(1px, 0, 0);
    }

    90% {
      transform: translate3d(-1px, 0, 0);
    }
  }
}

@mixin jump($name) {
  @keyframes #{$name} {
    from,
    to {
      transform: translate3d(0, 0, 0);
    }

    10%,
    80% {
      transform: translate3d(0, 0, 0);
    }

    60% {
      transform: translate3d(0, -8px, 0) scale(1.05);
    }

    90% {
      transform: translate3d(0, 0, 0) scale(1);
    }
  }
}

@mixin zoom-inn($name) {
  @keyframes #{$name} {
    from {
      transform: scale3d(.3, .3, .3);
    }

    80% {
      transform: scale3d(1.1, 1.1, 1.1);
    }
  }
}

@mixin ripple-out($name) {
  @keyframes #{$name} {
    100% {
      top: -6px;
      right: -6px;
      bottom: -6px;
      left: -6px;
      opacity: 0;
    }
  }
}
