@mixin btn-transactional(
  $bg-default: $colour-brand-blue,
  $bg-hover: $colour-blue-hover,
  $bg-pressed: $colour-blue-pressed,
  $text-default: $colour-neutral-white,
  $text-hover: $colour-neutral-white
) {
  @include btn-transactional-idle($bg-default, $text-default);

  &:focus:not(:disabled),
  &:hover:not(:disabled) {
    @include btn-transactional-hover($bg-hover, $text-hover);
  }

  &:active:not(:disabled) {
    @include btn-transactional-pressed($bg-pressed);
  }
}

@mixin btn-transactional-idle(
  $bg-default: $colour-brand-blue,
  $text-default: $colour-neutral-white
) {
  @include btn-state($bg-default, $text-default, $border-radius-xs, #{$c-spacing-m} $c-spacing-l);
}

@mixin btn-transactional-hover(
  $bg-hover: $colour-blue-hover,
  $text-hover: $colour-neutral-white
) {
  @include btn-state($bg-hover, $text-hover, $border-radius-s);
}

@mixin btn-transactional-pressed($bg-pressed: $colour-blue-pressed) {
  @include btn-state($bg-pressed, "", $border-radius-l);
}
