@mixin formelement($padding: #{$c-spacing-xl - $border-thickness-default-rem} #{$c-spacing-m} #{$c-spacing-xs - $border-thickness-default-rem}) {
  display: block;
  width: 100%;
  margin: 0;
  outline: 0;
  padding: $padding;
  border: $border-thickness-default solid $border-colour-idle;
  border-radius: $border-radius-s;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  transition: color $animation-normal, border $animation-normal, padding $animation-normal;

  &.full-width {
    display: block;
    width: 100%;
  }

  &.quantity-width {
    float: right;
    width: 6rem;
    min-width: 6rem;
  }

  &:hover {
    border-color: $border-colour-hover;
  }

  &:active,
  &:focus {
    border-width: $border-thickness-double;
    border-radius: $border-radius-m;
    border-color: $colour-brand-blue;
    padding: #{$c-spacing-xl - $border-thickness-double-rem} #{$c-spacing-m} #{$c-spacing-xs - $border-thickness-double-rem};
  }
}
