@import "./node_modules/@ikea-ux/variables/style.scss";

.accordion {
  list-style: none;
  width: 100%;
  margin: 0;
  padding: 0;
  background: transparent;
  border-top: $border-thickness-default solid $border-colour-idle;
  border-bottom: $border-thickness-default solid $border-colour-idle;

  &__light {
    background: $colour-neutral-white;
    border: $border-thickness-default solid $border-colour-idle;
    border-radius: $border-radius-xs;
  }

  &__item {
    transition: all $animation-normal $animation-timing;
    border-top: $border-thickness-default solid $border-colour-idle;
    margin: 0;
    outline: 0;
    padding: 0;
  }

  &__item:first-child {
    border-top: 0;
  }

  &__item--active {
    overflow: visible;
  }

  &__item--active > .accordion__content {
    padding-top: $c-spacing-m;
    padding-bottom: $c-spacing-m;
    height: auto;
    visibility: visible;
    opacity: 1;
    transition: all $animation-normal $animation-timing;
  }

  &__heading {
    display: inline-block;
    display: flex;
    text-align: left;
    background: none;
    overflow: hidden;
    border: 0;
    cursor: pointer;
    width: 100%;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: row;
    padding: $c-spacing-m 0;
    outline-offset: .25rem;
    outline-width: .25rem;
  }

  &__light .accordion__heading {
    padding: $c-spacing-m;
  }

  &__heading:hover {
    background: $colour-neutral-grey-100;
  }

  &__heading:active {
    background: $colour-neutral-grey-100;
    color: $colour-neutral-grey-900;
  }

  &__title {
    margin: 0;
    text-align: left;
    font-weight: bold;
  }

  &__icon {
    flex-shrink: 0;
  }

  &__content {
    padding: 0 $c-spacing-xl;
    transition: all 300ms cubic-bezier(0, 0, .25, 1);
    height: 0;
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
  }

  &__light .accordion__content {
    padding-left: $l-spacing-m;
  }
}

@media screen and (min-width: $breakpoints-large) {
  .accordion {
    &__title {
      font-size: $title-font-size-h5;
      line-height: $title-line-height-h5;
    }
  }
}
