@import "./node_modules/@ikea-ux/variables/style.scss";

a {
  text-decoration: none;
  font-weight: $font-weight-bold;
  color: $colour-blue-idle;

  &:hover {
    color: $colour-blue-hover;
    text-decoration: underline;
  }

  &:focus,
  &:active {
    color: $colour-blue-pressed;
    text-decoration: underline;
  }

  &.link--disabled:focus:before {
    border: 0;
  }

  &.link--xsmall {
    @include body-xs();
    font-weight: normal;
  }

  &.link--black {
    color: $colour-neutral-grey-900;

    &:hover,
    &:focus,
    &:active {
      color: $colour-neutral-grey-800;
    }
  }

  &.link--grey {
    color: $colour-neutral-grey-700;

    &:hover,
    &:focus,
    &:active {
      color: $colour-neutral-grey-900;
    }
  }

  &.link--white {
    color: $colour-neutral-white;

    &:hover,
    &:focus,
    &:active {
      color: $colour-neutral-white;
    }
  }

  &[role="button"].link--disabled {
    cursor: default;
    pointer-events: none;
    opacity: .4;

    &:hover {
      color: $colour-blue-hover;
    }

    &:focus,
    &:active {
      color: $colour-blue-pressed;
    }

    &.link--black {
      color: $colour-neutral-grey-900;

      &:hover,
      &:focus,
      &:active {
        color: $colour-neutral-grey-800;
      }
    }

    &.link--grey {
      color: $colour-neutral-grey-700;

      &:hover,
      &:focus,
      &:active {
        color: $colour-neutral-grey-700;
      }
    }

    &.link--white {
      color: $colour-neutral-white;

      &:hover,
      &:focus,
      &:active {
        color: $colour-neutral-white;
      }
    }
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  a {
    margin-bottom: 0;
    text-decoration: underline;

    .svg-icon {
      vertical-align: middle;
    }

    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }
  }
}

p {
  a {
    margin-bottom: 0;
    text-decoration: underline;
    line-height: 1.5;

    .svg-icon {
      vertical-align: middle;
    }

    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }
  }
}
