select {
  @include formelement();
  width: 100%;
  background: $colour-neutral-white;

  &:disabled {
    opacity: .4;
    pointer-events: none;

    ~ label {
      opacity: .4;
    }

    ~ a {
      pointer-events: none;
      opacity: .4;
    }

    ~ .svg-icon {
      opacity: .4;
    }
  }
}

select::-ms-expand {
  display: none;
}

select + label {
  pointer-events: none;
}

select ~ .svg-icon {
  pointer-events: none;
}

@-moz-document url-prefix("") {
  select {
    text-indent: -2px;
  }
}
