@import "./node_modules/@ikea-ux/variables/style.scss";

.dataTable {
  &-dropdown {
    label {
      display: inline-block;
      width: 100%;
    }
  }

  &-search {
    input {
      width: 100%;
    }
  }

  &-top:after,
  &-bottom:after {
    clear: both;
    content: " ";
    display: table;
  }

  &-sorter {
    color: $colour-neutral-grey-900;
    text-decoration: none;

    &:hover,
    &:active,
    &:focus {
      text-decoration: underline;
      color: $colour-neutral-grey-900;
    }
  }

  &-pagination {
    li {
      display: inline-block;
      list-style-type: none;
      margin-right: $c-spacing-m;

      a {
        text-decoration: none;

        &:hover,
        &:active,
        &:focus {
          text-decoration: underline;
        }
      }
    }
  }

  &-info {
    width: 100%;
  }
}

@media screen and (min-width: $breakpoints-small) {
  .dataTable {
    &-dropdown {
      float: left;

      label {
        display: inline-block;
      }
    }

    &-search {
      float: right;

      input {
        width: inherit;
      }
    }
  }
}
