@import "./node_modules/@ikea-ux/animations/style.scss";
@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./lib/_mixins.scss";

p a.btn,
p .btn {
  margin-bottom: 0;
}

.btn,
a.btn {
  font-weight: bold;
  position: relative;
  font-size: .875rem;
  line-height: 1.75;
  @include btn-base();
  @include transition(background-color border-radius width, $animation-normal);

  &--filled {
    @include btn-filled();

    &-light {
      @include btn-filled(
        $colour-neutral-white,
        $colour-neutral-grey-200,
        $colour-neutral-grey-300,
        $colour-neutral-grey-900,
        $colour-neutral-grey-900,
        $colour-neutral-grey-900
      );
    }
  }

  &--search {
    border-left: 0;
    padding-left: $l-spacing-s;
    padding-right: $l-spacing-s;
    background: $colour-blue-idle;
    color: $colour-neutral-white;
    border-radius: 0 $border-radius-xs $border-radius-xs 0;
    padding: .625rem $c-spacing-l;

    &:focus:not(:disabled),
    &:hover:not(:disabled) {
      color: $colour-neutral-white;
      background-color: $colour-blue-hover;
      border-radius: 0 $border-radius-s $border-radius-s 0;

      .svg-icon {
        fill: $colour-neutral-white;
      }
    }

    &:active:not(:disabled) {
      background: $colour-blue-pressed;
      border-radius: $border-radius-l;
      border-radius: 0 $border-radius-l $border-radius-l 0;
    }

    .svg-icon {
      fill: $colour-neutral-white;
    }
  }

  &--search.btn--loading {
    border-radius: 0 $border-radius-s $border-radius-s 0;
  }

  &--search-white {
    padding: $c-spacing-2xs;
    background: none;
  }

  &--icon {
    @include btn-icon();
  }

  &--plain {
    padding: $c-spacing-2xs;
    background: none;

    &-light {
      .svg-icon {
        fill: $colour-neutral-white;
      }
    }
  }

  &--icon,
  &--plain {
    .btn__label {
      text-indent: -9999px;
      display: inline-block;
      width: 0;
      height: 0;
      overflow: hidden;
    }
  }

  &--outlined {
    @include btn-outlined();

    &-light {
      @include btn-outlined(
        $colour-neutral-white,
        $colour-neutral-white,
        $colour-neutral-grey-100,
        $colour-neutral-white,
        $colour-neutral-grey-900,
        $colour-neutral-grey-900
      );
    }

    &.btn--loading {
      &:focus:not(:disabd),
      &:hover:not(:disabled) {
        background-color: transparent;
      }
    }
  }

  &--transactional {
    @include btn-transactional();

    .btn__label {
      display: none;
    }

    .btn__copy .svg-icon {
      margin: 0;
    }
  }

  &--danger {
    @include btn-filled($colour-red-idle, $colour-red-hover, $colour-red-pressed);
  }

  &--text {
    @include btn-text(transparent, $colour-neutral-grey-200, $colour-neutral-grey-300, $colour-neutral-grey-900, $colour-neutral-grey-800);
  }

  &--loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    pointer-events: none;
    border-radius: $border-radius-s;

    .btn__copy {
      height: 0;
      overflow: hidden;
      display: block;

      .svg-icon {
        display: none;
      }
    }
  }

  &--mobile-100 {
    width: 100%;
  }
}

@media screen and (min-width: $breakpoints-medium) {
  .btn,
  a.btn {
    &--transactional .btn__label {
      display: inline-block;
    }

    &--transactional .btn__copy .svg-icon {
      margin-right: $c-spacing-xs;
    }

    &--mobile-100 {
      width: auto;
    }
  }
}
