.table--mobile-scroll {
  overflow-x: auto;
  position: relative;

  thead {
    display: table-header-group;
  }

  tbody {
    display: table-row-group;

    th,
    td {
      padding-top: $l-spacing-2xs;
      padding-bottom: $l-spacing-2xs;
    }
  }

  th,
  td {
    display: table-cell;
    padding-left: $l-spacing-2xs;
    padding-right: $l-spacing-2xs;
  }

  tr {
    display: table-row;

    &:not(:last-child) {
      border-bottom: none;
    }

    &:first-child {
      th,
      td {
        padding-top: $l-spacing-2xs;
      }
    }
  }
}

@media screen and (min-width: $breakpoints-small) {
  .table--mobile-scroll {
    th,
    td {
      float: none;
    }
  }
}
