input {
  color: $colour-neutral-grey-900;
  width: 100%;
}

input:not([type="range"]) {
  @include formelement();
}

input:not([type="range"]):not([type="number"]) {
  ~ .btn--plain {
    padding: $c-spacing-m;
  }
}

input[type="submit"] {
  width: auto;
}

input:disabled {
  opacity: .4;
  pointer-events: none;

  ~ label {
    opacity: .4;
  }

  ~ a {
    pointer-events: none;
    opacity: .4;
  }

  ~ .input-wrap__icon {
    opacity: .4;
  }
}

/*
* Placeholder styling for Gecko & Webkit
*/

input::-webkit-input-placeholder {
  transition: color $animation-normal;
  color: transparent;
}

input::-moz-input-placeholder {
  transition: color $animation-normal;
  color: transparent;
}

input:-moz-input-placeholder {
  transition: color $animation-normal;
  color: transparent;
}

input::placeholder {
  transition: color $animation-normal;
  color: transparent;
}

input:-ms-input-placeholder {
  color: transparent;
}

input:focus::-moz-input-placeholder {
  color: $colour-neutral-grey-300;
  transition-delay: $animation-normal;
}

input:focus:-moz-input-placeholder {
  color: $colour-neutral-grey-300;
  transition-delay: $animation-normal;
}

input:focus::-ms-input-placeholder {
  color: $colour-neutral-grey-300;
  transition-delay: $animation-normal;
}

input:focus::placeholder {
  color: $colour-neutral-grey-300;
  transition-delay: $animation-normal;
}
