textarea {
  @include formelement();
  color: $colour-neutral-grey-900;
  font-family: inherit;
  font-size: inherit;
  resize: none;
  overflow: hidden;
  line-height: 1.5;

  &:disabled {
    opacity: .4;
    pointer-events: none;
    cursor: not-allowed;

    ~ label {
      opacity: .4;
    }
  }
}

.textarea--overflow {
  overflow: auto;
}

/*
* Placeholder styling for Gecko & Webkit
*/

textarea:-moz-placeholder,
textarea:-webkit-input-placeholder {
  color: $colour-neutral-grey-700;
}

textarea::-webkit-input-placeholder {
  opacity: 0;
  transition: all $animation-normal;
  color: $colour-neutral-grey-300;
}

textarea::-moz-input-placeholder {
  opacity: 0;
  transition: all $animation-normal;
  color: $colour-neutral-grey-300;
}

textarea:-moz-input-placeholder {
  opacity: 0;
  transition: all $animation-normal;
  color: $colour-neutral-grey-300;
}

textarea:-ms-input-placeholder {
  opacity: 0;
  transition: all $animation-normal;
  color: $colour-neutral-grey-300;
}

textarea:focus::-webkit-input-placeholder {
  opacity: 1;
  transition-delay: $animation-normal;
}

textarea:focus::-moz-input-placeholder {
  opacity: 1;
  transition-delay: $animation-normal;
}

textarea:focus:-moz-input-placeholder {
  opacity: 1;
  transition-delay: $animation-normal;
}

textarea:focus::-ms-input-placeholder {
  opacity: 1;
  transition-delay: $animation-normal;
}
