@function adjustcolour($basecolor, $l) {
  $returnColor: $basecolor;

  @if $l < 0 {
    $returnColor: mix(#000, $basecolor, abs($l));
  } @else {
    $returnColor: mix( #fff, $basecolor, abs($l));
  }

  @return $returnColor;
}

@function px2rem($value) {
  $retval: $value * 100;
  @return ($retval / 16) / 100 + rem;
}

@function px2em($value) {
  $retval: $value * 100;
  @return ceil($retval / 16) / 100 + em;
}

@function px2lineheight($font-size, $line-height) {
  $retval: $line-height/$font-size;
  @return $retval;
}
