@import "./node_modules/@ikea-ux/variables/style.scss";

label {
  display: inline-block;
  color: $colour-neutral-grey-700;
}

textarea ~ label,
input[type="text"] ~ label,
input[type="email"] ~ label,
input[type="password"] ~ label,
input[type="tel"] ~ label,
input[type="url"] ~ label,
select ~ label {
  transition: all $animation-normal;
}

label em {
  margin: 0;
  color: $colour-neutral-grey-500;
  font-weight: normal;
  font-style: normal;
}

.required {
  color: $colour-feedback-error;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="url"] {
  &:focus ~ label {
    top: $c-spacing-m;
    font-size: $body-font-size-xs;
    color: $colour-brand-blue;
  }
}

textarea:focus ~ label {
  border-radius: $border-radius-m $border-radius-m 0 0;
  left: $border-thickness-default;
  right: $border-thickness-default;
  top: $border-thickness-default;
  font-size: $body-font-size-xs;
  color: $colour-brand-blue;
  padding: #{$c-spacing-m - $border-thickness-default-rem} #{$c-spacing-m} 0 $c-spacing-m;
  animation-delay: $animation-fast;
}
