
@mixin offscreen {
  height: .0625rem;
  left: -999rem;
  overflow: hidden;
  position: absolute;
  top: auto;
  width: .0625rem;
  @at-root {
    [dir="rtl"] & {
      left: auto;
      right: -999rem;
    }
  }
}

@mixin flat-list {
  list-style: none;
  margin: 0;
}

@mixin ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 90%;
}

@mixin cf {
  &:after {
    clear: both;
    content: ".";
    display: block;
    height: 0;
    visibility: hidden;
  }
}
