@import "./radio_variables.scss";

fieldset .radio {
  margin-bottom: $c-spacing-m;
}

input[type="radio"] {
  position: absolute;
  top: 0;
  left: -9999px;
  margin: 0;
  outline: 0;
}

.radio {
  position: relative;

  input[type="radio"] {
    position: absolute;
    left: -$c-spacing-m;
    top: -$c-spacing-s;
    width: 3rem;
    height: 3rem;
    display: inline-block;
    border: 0;
    opacity: 0;
    padding: 0;
    z-index: 1;
    cursor: pointer;
  }

  &__fill {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 1rem;
    height: 1rem;
    border-radius: $border-radius-2xl;
    background: $radio-button-background-idle;
    border: $border-thickness-default solid $radio-button-border-idle;
    transition: border $animation-fast, background $animation-fast, border-radius $animation-fast;
  }

  &__symbol {
    position: absolute;
    top: $c-spacing-2xs;
    left: 0;
    display: block;
    width: 1rem;
    height: 1rem;
    transition: border $animation-fast, background $animation-fast, border-radius $animation-fast;

    .radio__fill-highlight,
    .radio__fill-dot {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      content: "";
      display: block;
      border-radius: $border-radius-2xl;
    }

    .radio__fill-highlight {
      transition: all $animation-normal;
      width: 0;
      height: .875rem;
    }

    .radio__fill-dot {
      width: 0px;
      height: 0px;
    }
  }

  input[type="radio"]:hover {
    ~ .radio__symbol .radio__fill-highlight {
      background: $radio-button-outer-highlight-hover;
      width: 2rem;
      height: 2rem;
      transition: all $animation-normal;
    }

    ~ .radio__symbol .radio__fill-dot {
      background: $radio-button-inner-highlight-hover;
      height: .25rem;
      width: .25rem;
    }
  }

  input[type="radio"]:checked {
    ~ .radio__symbol .radio__fill {
      background: $radio-button-background-checked;
    }

    ~ .radio__symbol .radio__fill-highlight {
      width: 0;
      height: 0;
      content: "";
      display: block;
      transition: all $animation-slow;
    }

    ~ .radio__symbol .radio__fill-dot {
      background: $radio-button-inner-highlight-checked;
      content: "";
      width: .25rem;
      height: .25rem;
      display: block;
    }
  }

  input[type="radio"]:active:not(:checked):not(:disabled) {
    ~ .radio__symbol .radio__fill {
      background: $radio-button-background-pressed;
    }

    ~ .radio__symbol .radio__fill-highlight {
      background: $radio-button-outer-highlight-pressed;
    }

    ~ .radio__symbol .radio__fill-dot {
      background-color: $radio-button-inner-highlight-checked;
      height: .25rem;
      width: .25rem;
    }
  }

  input[type="radio"]:disabled {
    &:hover,
    &:checked {
      ~ .radio__symbol .radio__fill-highlight {
        display: none;
      }

      ~ .radio__symbol {
        opacity: .4;
      }
    }

    &:not(:checked) {
      ~ .radio__symbol {
        opacity: .4;

        &:after {
          display: none;
        }
      }
    }

    ~ .radio__label label {
      opacity: .4;
      cursor: not-allowed;
    }
  }

  .radio__label {
    position: relative;
    display: block;
    padding-left: $c-spacing-xl;
  }

  label {
    cursor: pointer;
    color: $colour-neutral-grey-900;
  }
}

[dir="rtl"] {
  input[type="radio"] {
    left: auto;
    right: -9999px;
  }

  .radio {
    input[type="radio"] {
      right: 0;
      left: initial;
    }

    label {
      padding-right: $c-spacing-xl;
      padding-left: 0;
    }

    .radio__symbol {
      right: 0;
      left: initial;
    }
  }
}
