@mixin btn-icon(
  $bg-default: transparent,
  $bg-hover: $colour-neutral-grey-200,
  $bg-pressed: $colour-neutral-grey-300
) {
  @include btn-icon-idle($bg-default);

  &.btn--loading {
    background: $bg-hover;
    border-radius: $border-radius-2xl;
  }

  &:focus:not(:disabled),
  &:hover:not(:disabled) {
    @include btn-icon-hover($bg-hover);
  }

  &:active:not(:disabled) {
    @include btn-icon-pressed($bg-pressed);
  }
}

@mixin btn-icon-idle($bg-default: transparent) {
  @include btn-state($bg-default, "", $border-radius-2xl, $c-spacing-s);
  line-height: 0;
  position: relative;

  .btn__copy .svg-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    right: 0;
    bottom: 0;
    display: block;
    margin: 0;
    line-height: 0;
    transform: translate(-50%, -50%);
  }

  .btn__copy {
    display: block;
    height: 1.5rem;
    width: 1.5rem;
    line-height: 0;
  }

  .btn__icon-bg {
    position: absolute;
    left: 0;
    top: 0;
    display: block;
    border-radius: $border-radius-2xl;
    width: 100%;
    height: 100%;
    transform: scale(0);
    @include transition(transform background-color, $animation-normal);
  }
}

@mixin btn-icon-hover($bg-hover: $colour-neutral-grey-200) {
  .btn__icon-bg {
    @include btn-state($bg-hover);
    transform: scale(1);
  }
}

@mixin btn-icon-pressed($bg-pressed: $colour-neutral-grey-300) {
  .btn__icon-bg {
    @include btn-state($bg-pressed);
    transform: scale(.8333);
  }
}
