@import "./node_modules/@ikea-ux/variables/style.scss";

.file-upload {
  padding: $c-spacing-m;
  display: inline-block;
  display: flex;
  max-width: 23.4375rem;
  border: $border-thickness-default solid $border-colour-idle;

  &__icon {
    margin-right: $c-spacing-m;
  }

  &__content {
    width: 100%;
  }

  &__icon-style {
    position: relative;
    background: $colour-neutral-grey-900;
    border-radius: $border-radius-2xl;
    display: inline-block;
    height: 2.5rem;
    width: 2.5rem;

    .svg-icon {
      position: absolute;
      fill: $colour-neutral-white;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  p {
    color: $colour-neutral-grey-700;
    margin-bottom: 0;
  }

  &__file-name {
    overflow: hidden;
    text-overflow: ellipsis;
  }

  h4 {
    margin-bottom: 0;
  }

  input[type="file"] {
    width: .25rem;
    height: .25rem;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }

  label:not(:disabled) {
    cursor: pointer;
  }

  .loading-bar,
  .file-upload__remove-file {
    display: none;
  }

  &__remove-file {
    position: relative;
  }

  &__file {
    position: relative;
    display: inline-block;
    display: flex;
    margin-bottom: $c-spacing-l;
    align-items: center;

    .loading-bar {
      width: 100%;
    }
  }

  &--uploading {
    .loading-bar {
      display: block;
    }

    .file-upload__remove-file {
      vertical-align: middle;
      display: inline-block;
      margin-left: $c-spacing-xs;
    }
  }

  &--uploaded {
    .file-upload__status-label {
      display: none;
    }

    .file-upload__file {
      margin-top: $c-spacing-m;
    }

    .file-upload__remove-file {
      display: inline-block;
      margin-left: $c-spacing-xs;
      padding: 0;
    }
  }
}
