@mixin btn-base {
  text-align: center;
  display: inline-block;
  text-decoration: none;
  border: 0;

  &:disabled {
    opacity: .4;
  }

  .svg-icon {
    vertical-align: top;
  }
}

@mixin btn-state(
  $bg-colour,
  $text-colour: "",
  $border-radius: "",
  $padding: "",
  $border-style: ""
) {
  background: $bg-colour;
  @if ($text-colour != "") {
    color: $text-colour;
  }

  @if ($border-radius != "") {
    border-radius: $border-radius;
  }

  @if ($padding != "") {
    padding: $padding;
  }

  @if ($border-style != "") {
    border: $border-style;
  }

  .svg-icon {
    fill: $text-colour;
  }
}

@import "./btn-filled.scss";
@import "./btn-icon.scss";
@import "./btn-outlined.scss";
@import "./btn-search.scss";
@import "./btn-text.scss";
@import "./btn-transactional.scss";
