// Disabled form fields
:disabled {
  cursor: not-allowed;
}

.form-field {
  display: block;
  margin-bottom: $c-spacing-m;

  > input,
  > .input-wrap > input,
  > textarea,
  > .input-wrap > textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
  }

  .input-wrap {
    margin-bottom: $c-spacing-xs;
  }

  .inline-text {
    margin-left: $l-spacing-2xs;
    margin-right: $l-spacing-2xs;
  }
}
