@import "./node_modules/@ikea-ux/variables/style.scss";

.block-quote {
  text-align: center;

  &__quote {
    position: relative;
  }

  &__background {
    font-family: "Georgia", "Times New Roman", Times, serif;
    font-size: 20rem;
    line-height: 1;
    color: $colour-brand-yellow;
    position: absolute;
    left: $c-spacing-xl;
    right: $c-spacing-xl;
    top: -$c-spacing-xl;
  }

  &__divider {
    margin-top: $c-spacing-l;
    margin-bottom: $c-spacing-l;
    width: 5rem;
    height: $border-thickness-default;
    margin-left: auto;
    margin-right: auto;
    background: $colour-neutral-grey-200;
  }

  picture,
  img {
    max-width: 6.25rem;
    border-radius: $border-radius-2xl;
  }

  picture {
    margin-bottom: $c-spacing-2xl;
  }
}

blockquote {
  padding-top: $c-spacing-xl;
  position: relative;
}

cite span {
  display: block;
  font-size: $body-font-size-m;
  color: $colour-neutral-grey-700;
}

cite {
  font-size: $title-font-size-h5;
  line-height: $title-line-height-h5;
  font-style: normal;
  font-weight: $font-weight-bold;
}

@media screen and (min-width: $breakpoints-medium) {
  .block-quote {
    img,
    picture {
      max-width: 12.5rem;
    }

    &--compact {
      display: inline-block;
      display: flex;
      text-align: left;

      .block-quote__background {
        left: 0;
        right: 0;
      }

      .block-quote__quote {
        margin-left: $c-spacing-xl;
      }

      blockquote {
        background-position: left top;
      }

      .block-quote__divider {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }
}
