@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/keyframes.scss";

@include fade-in-out(tooltip-fade-in-out);

.tooltip {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  max-width: 18.75rem;
  border-radius: $border-radius-s;
  opacity: 0;
  background: $colour-neutral-grey-700;
  color: $colour-neutral-white;
  font-size: $body-font-size-xs;
  padding: $c-spacing-xs $c-spacing-s;
  z-index: 3001;
  animation: tooltip-fade-in-out both $animation-timing;
  animation-duration: $animation-normal;

  p:last-child {
    margin-bottom: 0;
  }

  &:after {
    content: " ";
    position: absolute;
    background: $colour-neutral-grey-700;
    left: 0;
    bottom: -4px;
    right: 0;
    transform: rotate(45deg);
    width: .5rem;
    height: .5rem;
    margin: 0 auto;
  }

  &__left-edge:after {
    left: 10%;
    right: auto;
  }

  &__right-edge:after {
    left: auto;
    right: 10%;
  }

  &__top-edge:after {
    top: -4px;
    bottom: auto;
    transform: rotate(225deg);
  }

  &.show {
    display: block;
    opacity: 1;
  }
}

[data-tooltip] {
  cursor: pointer;
  display: inline-block;
}
