.error {
  strong {
    font-weight: normal;
    display: block;
    color: $colour-feedback-error;
  }

  .input-wrap {
    input,
    select,
    textarea {
      &:not(:focus) {
        border-width: $border-thickness-focus;
        border-color: $colour-feedback-error;
        padding: #{$c-spacing-xl - $border-thickness-focus-rem} #{$c-spacing-m} #{$c-spacing-xs - $border-thickness-focus-rem};
      }
    }
  }

  .input-wrap input:not(:focus) ~ label {
    top: $c-spacing-m + $c-spacing-2xs;
    left: $c-spacing-m + $border-thickness-double-rem;
  }

  .input-wrap--labelled input:not(:focus) ~ label {
    top: $c-spacing-m;
  }

  .input-wrap textarea:not(:focus) ~ label {
    top: $border-thickness-double;
    left: $border-thickness-double;
    right: $border-thickness-double;
    padding-top: $c-spacing-m + .125rem;
  }

  .input-wrap--labelled textarea:not(:focus) ~ label {
    padding-top: $c-spacing-m - $border-thickness-double-rem;
  }
}
