@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/style.scss";

.tabs {
  &--fixed-space {
    .tabs__tab {
      flex: auto;
    }
  }

  &__tab {
    position: relative;
    bottom: -$border-thickness-default;
    background: none;
    border: 0;
    display: inline-block;
    align-items: center;
    flex-direction: column;
    text-align: center;
    font-weight: bold;
    padding-top: $c-spacing-m;
    padding-bottom: $c-spacing-xs;
    padding-left: 0;
    padding-right: 0;
    border-bottom: $border-thickness-default solid transparent;

    .svg-icon {
      width: 2rem;
      height: 2rem;
      fill: $colour-neutral-grey-900;
      display: block;
      margin: 0 auto;
    }

    &:not(.tabs__tab--active):hover {
      border-bottom: $border-thickness-default solid $colour-brand-blue;
    }

    &:focus {
      z-index: 1;
    }

    .svg-icon {
      margin-bottom: $c-spacing-xs;
    }

    &--active {
      color: $colour-brand-blue;

      .tabs__active-tab-line {
        position: absolute;
        left: 0;
        right: 0;
        bottom: -$border-thickness-default;
        content: " ";
        display: block;
        border-bottom: $border-thickness-double solid $colour-brand-blue;
      }

      &:hover {
        cursor: default;
      }

      &:before {
        transform: scaleX(1);
        border-bottom: $border-thickness-double solid $colour-brand-blue;
      }

      .svg-icon {
        fill: $colour-brand-blue;
      }
    }

    + .tabs__tab {
      margin-left: $l-spacing-m;
    }
  }

  &__list {
    position: relative;
    display: inline-block;
    display: flex;
    width: 100%;
    border-bottom: $border-thickness-default solid $colour-neutral-grey-300;
  }

  &__panel {
    padding: $c-spacing-xl 0;
    outline: none;
    position: relative;
  }
}

@media screen and (min-width: $breakpoints-medium) {
  .tabs {
    &__tab {
      .svg-icon {
        width: 1.5rem;
        height: 1.5rem;
      }
    }
  }
}
