@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./_extensions.scss";

ol,
ul {
  line-height: 1.5;
  padding-left: $l-spacing-xs;

  a {
    text-decoration: none;

    &:hover,
    &:active,
    &:focus {
      text-decoration: underline;
    }
  }

  li {
    padding-left: $c-spacing-xs;
  }
}

li {
  margin-bottom: $c-spacing-xs;
}

.list {
  &--xsmall {
    @include body-xs();

    a {
      font-weight: normal;
    }
  }

  &--font-normal {
    a {
      font-weight: normal;
    }
  }

  &--plain {
    @extend %plain;
  }

  &--horizontal {
    margin-bottom: 0;

    > li {
      @extend %horizontal-listitem;
    }
  }

  &--line-separated {
    > li:not(:last-child):after {
      content: "";
      display: inline-block;
      font-weight: normal;
      margin: 0 $c-spacing-m;
      background: $border-colour-idle;
      height: 1.5rem;
      width: $border-thickness-default;
      vertical-align: top;
    }

    > li {
      margin: 0 0  $c-spacing-xs 0;
      padding: 0;
    }

    &.list--xsmall {
      > li:not(:last-child):after {
        margin: 0 $c-spacing-xs;
        height: 1rem;
      }
    }
  }
}
