@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/keyframes.scss";

@include delayed-fade-in(modal-delayed-fade-in);

.modal {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: $colour-neutral-white;
  z-index: 9001;
  padding: $l-spacing-m $c-spacing-l $c-spacing-l $c-spacing-l;
  overflow: auto;
  display: none;
  text-align: left;

  &__buttons {
    display: flex;
    flex-direction: column-reverse;
    margin-top: $c-spacing-m;
    flex: 1 0 auto;
    justify-content: flex-end;

    .btn {
      width: 100%;
      margin-top: $c-spacing-m;
    }
  }

  &__video {
    z-index: 9001;
    background: rgba(0, 0, 0, .8);

    iframe {
      max-width: 100%;
    }
  }

  &__alert {
    box-shadow: none;
    overflow: visible;
    padding: $c-spacing-m $c-spacing-m $c-spacing-l $c-spacing-m;

    .modal__content {
      overflow: auto;
    }

    .modal__title {
      padding-top: $c-spacing-l;
    }
  }

  &__close {
    position: absolute;
    right: $c-spacing-xs;
    top: $c-spacing-xs;
    margin-bottom: 0;
  }

  &__video-close {
    position: absolute;
    top: $c-spacing-xs;
    right: $c-spacing-xs;
    font-weight: normal;
    margin-bottom: 0;
    animation-name: modal-delayed-fade-in;
    animation-duration: .6s;
    animation-timing-function: ease-in;
    z-index: 1;
  }

  &__video-close,
  &__close {
    .svg-icon {
      width: 2.5rem;
      height: 2.5rem;
    }
  }
}

@media screen and (min-width: $breakpoints-medium) {
  .modal {
    border-radius: $border-radius-xs;
    padding-top: $c-spacing-l;

    &__buttons {
      flex-direction: row;
      justify-content: flex-start;
      flex-wrap: wrap;

      .btn {
        width: initial;
        margin-right: $c-spacing-m;
      }
    }

    &__video {
      padding: 0;
      overflow: visible;
      background: none;
      box-shadow: none;

      .modal__content {
        position: relative;
        padding-bottom: 56.25%; /* 16:9 */
        padding-top: 0;
        height: 0;
      }
    }

    &__content {
      max-height: 50%;
    }

    &__alert {
      padding: $c-spacing-xl $c-spacing-xl $c-spacing-2xl $c-spacing-xl;
      border-radius: $border-radius-m;

      .modal__title {
        padding-top: 0;
      }
    }

    &__video-close {
      position: absolute;
      right: 0;
      top: -$l-spacing-s;

      .svg-icon {
        width: 1.5rem;
        height: 1.5rem;
        fill: $colour-neutral-white;
      }
    }

    &__close {
      position: absolute;

      .svg-icon {
        width: 1.5rem;
        height: 1.5rem;
      }
    }
  }
}
