@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/style.scss";

.expander {
  display: flex;

  &__btn {
    position: relative;
    width: 100%;
    display: inline-block;
    margin: 0;
    border: 0;
    background: none;
    text-align: center;
    padding: 0;
    line-height: 1.5;

    &--text-toggle {
      width: initial;
      padding: 0;

      .expander__line-behind {
        display: none;
      }

      .expander__btn-icon {
        padding-left: 0;
      }

      &.expander__btn--opened {
        .svg-icon {
          transform: rotate(-180deg);
        }
      }
    }

    span {
      background: $colour-neutral-white;
      font-weight: bold;
      vertical-align: middle;
    }
  }

  &__line-behind {
    position: absolute;
    content: "";
    top: 50%;
    left: 0;
    right: 0;
    bottom: 0;
    border-top: 2px solid $colour-neutral-grey-900;
    margin: 0 auto;
    width: 100%;
  }

  &__btn-icon {
    padding-left: $c-spacing-m;
  }

  &__btn-label {
    padding-left: $c-spacing-xs;
    padding-right: $c-spacing-m;
  }

  &__content {
    @include transition(max-height, $animation-slow, ease);
    position: relative;
    overflow: hidden;
    max-height: 0;

    &--closed {
      &.expander__content--faded:before {
        background: linear-gradient(
          to top,
          rgba(255, 255, 255, 1) 0%,
          rgba(255, 255, 255, 1) 10%,
          rgba(255, 255, 255, 0) 100%
        );
        @include transition(background, $animation-slow, ease);
        position: absolute;
        top: -$c-spacing-xs;
        bottom: 0;
        left: 0;
        right: 0;
        content: "";
        display: block;
      }
    }

    &--opened {
      max-height: 100%;

      &.expander__content--faded:after {
        background: linear-gradient(
          rgba(255, 255, 255, 0),
          rgba(255, 255, 255, 0)
        );
      }
    }
  }
}
