@import "./node_modules/@ikea-ux/variables/style.scss";

progress {
  height: .5rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  color: $colour-brand-blue;
  width: 100%;
  display: inline-block;
  border: none;
  background: adjustcolour($colour-brand-blue, 80);
}

progress::-webkit-progress-bar {
  background-color: adjustcolour($colour-brand-blue, 80);
  height: .5rem;
}

progress::-moz-progress-bar {
  background-color: $colour-brand-blue;
}

progress::-webkit-progress-value {
  background-color: $colour-brand-blue;
}

progress::-moz-progress-value {
  background-color: $colour-brand-blue;
}

.loading-bar {
  &__fallback {
    position: relative;
    display: block;
    background-color: #e5edf9;
    border-radius: 0;
    width: 100%;
    height: .5rem;
  }

  &__fallback > span {
    display: block;
    text-indent: -9999px;
    background-color: $colour-brand-blue;
    border-radius: $border-radius-xs;
  }
}
