@import "./node_modules/@ikea-ux/animations/style.scss";
@import "./node_modules/@ikea-ux/variables/style.scss";

.banner {
  background: rgba($colour-neutral-grey-100, .9);
  color: $colour-neutral-grey-700;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  @include transition(transform, $animation-fast, ease-out);
  overflow: hidden;
  z-index: 7000;

  &__title {
    @include h4();
  }

  &__container {
    padding: $l-spacing-2xs;
    position: relative;
    max-width: $grid-max-width;
    margin: 0 auto;
  }

  &--bottom {
    bottom: 0;
    transform: translate(0, 100%);

    &.banner--open {
      transform: translate(0, 0);
    }
  }

  &--top {
    top: 0;
    bottom: initial;
    transform: translate(0, -100%);

    &.banner--open {
      transform: translate(0, 0);
    }
  }

  &--dark {
    background: rgba($colour-neutral-grey-900, .9);
    color: $colour-neutral-white;

    a {
      color: $colour-neutral-white;

      &:hover,
      &:focus,
      &:active {
        color: $colour-neutral-white;
      }
    }
  }

  &--light {
    background: $colour-neutral-grey-100;

    &__title {
      color: $colour-neutral-grey-900;
    }
  }

  .btn + .btn {
    margin-left: $c-spacing-m;
  }

  &--dismissable {
    .banner__content {
      padding-right: $l-spacing-m;
    }
  }

  &__close-btn {
    align-self: flex-start;
    position: absolute;
    top: $c-spacing-xs;
    right: $c-spacing-xs;
  }
}

@media screen and (min-width: $breakpoints-large) {
  .banner {
    &__container {
      display: flex;
    }

    &--dismissable {
      .banner__content {
        padding-right: 0;
      }
    }

    &__content {
      flex: 1 1 auto;
    }

    &__actions {
      margin-left: $l-spacing-2xs;
      flex: 1 0 auto;
      justify-self: flex-end;
      align-self: center;
      text-align: right;
      padding-right: $l-spacing-m;
    }
  }
}
