@import "./node_modules/@ikea-ux/variables/style.scss";

.quantity {
  width: 8rem;

  input:not([type=range]) {
    padding: 1.188rem 3rem;
    text-align: center;
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type=number] {
    -moz-appearance: textfield;
  }

  &__increase,
  &__decrease {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  &__increase {
    right: 1rem;
  }

  &__decrease {
    left: 1rem;
  }

  &__increase.disabled svg,
  &__decrease.disabled svg {
    fill: $colour-neutral-grey-700;
  }

  .link--leading-icon .svg-icon {
    margin: 0;
  }
}
