@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./_font.scss";

/**
  * Headings
  */

h1.page-headline {
  @include page-headline();
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: $c-spacing-xs;

  @at-root {
    h1,
    .h1 {
      @include h1();
    }

    h2,
    .h2 {
      @include h2();
    }

    h3,
    .h3 {
      @include h3();
    }

    h4,
    .h4 {
      @include h4();
    }

    h5,
    .h5,
    h6,
    .h6 {
      @include h5();
    }
  }
}

small {
  @include body-s();
  display: inline-block;
}

/**
  * Paragraphs
  */

p {
  margin-bottom: $c-spacing-xs;
}
