@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./_mixins";
@import "./_custom-normalize";

/**
   * Basic resets
   */
blockquote,
body,
caption,
div,
dl,
dt,
dd,
form,
fieldset,
h1,
h2,
h3,
h4,
h5,
h6,
html,
li,
ul,
ol,
p,
pre,
textarea,
table,
td,
th,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  margin: 0;
  padding: 0;
}

/**
   * HTML5 elements legacy reset
   */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  outline: none;
}

a,
button {
  position: relative;
}

dt {
  font-weight: bold;
}

dd:after {
  content: "";
  display: block;
}

fieldset,
table,
th,
td {
  border: 0;
}

th,
td {
  vertical-align: top;
}

caption,
th {
  font-weight: bold;
  text-align: left;
}

a,
a:visited {
  background-color: transparent;
  color: $colour-brand-blue;
}

a:hover {
  color: $colour-blue-hover;
}

a:focus,
a:active {
  color: $colour-blue-pressed;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 1rem;
}

input[type="search"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  border-radius: inherit;
}

button,
[type="submit"],
[type="reset"] {
  cursor: pointer;
}

i,
em {
  font-style: italic;
}

/**
   * Editorial images (left & right aligned)
   */
img.left {
  float: left;
  margin: 0 $l-spacing-2xs $l-spacing-2xs 0;
}

img.right {
  float: right;
  margin: 0 0 $l-spacing-2xs $l-spacing-2xs;
}

/**
  * Visually hidden (still visible to AT:s)
  */
.offscreen {
  @include offscreen;
}

.visuallyhidden {
  position: absolute;
  margin: -1px;
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  overflow: hidden;
  padding: 0;
  width: 1px;
  white-space: nowrap; /* 1 */

  .focusable:active,
  .focusable:focus {
    position: static;
    margin: 0;
    clip: auto;
    height: auto;
    overflow: visible;
    width: auto;
    white-space: inherit;
  }
}

/**
   * Easy clearing of floats
   * (see http://positioniseverything.net/easyclearing.html)
   */
.cf {
  @include cf();
}

.ellipsis {
  @include ellipsis();
}

.flat-list {
  @include flat-list();
}

hr {
  border: 0;
  height: .5rem;
  margin: $c-spacing-2xl 0;
  background: adjustcolour($colour-brand-blue, 80);
  box-shadow: inset 0 1px 0px 0 rgba(0, 0, 0, .09);
}
