input[type="search"] {
  padding: .6875rem $c-spacing-m;

  &:focus {
    ~ label {
      opacity: 0;
    }
    padding: #{.6875rem - $border-thickness-default-rem} $c-spacing-m;
  }

  ~ label {
    position: absolute;
    left: $c-spacing-m;
    top: $c-spacing-s;
  }
}

.search-actions {
  position: relative;
  display: flex;
}

.search-action-divider {
  position: absolute;
  right: $l-spacing-m;
  top: $c-spacing-2xs;
  display: inline-block;
  width: $border-thickness-default;
  height: 1.5rem;
  opacity: 0;
  @include transition(opacity);
  border-top: $border-thickness-default solid $colour-neutral-grey-300;
  border-bottom: $border-thickness-default solid $colour-neutral-grey-300;
  border-right: $border-thickness-default solid $colour-neutral-grey-300;
}

input[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

input[type="search"]::-moz-search-cancel-button {
  -moz-appearance: none;
}

input[type="search"]:focus::-webkit-input-placeholder {
  color: $colour-neutral-grey-300;
}

input[type="search"]:focus::-moz-input-placeholder {
  color: $colour-neutral-grey-300;
}

input[type="search"]:focus:-moz-input-placeholder {
  color: $colour-neutral-grey-300;
}

input[type="search"]:focus::-ms-input-placeholder {
  color: $colour-neutral-grey-300;
}

.input-wrap--filled-white-search,
.input-wrap--filled-blue-search {
  display: flex;

  &.input-wrap--labelled {
    label {
      opacity: 0;
      font-size: $body-font-size-m;
    }
  }

  .btn--plain {
    display: none;
    opacity: 0;
    pointer-events: none;
    @include transition(opacity);
  }
}

.input-wrap--filled-blue-search {
  display: flex;

  input[type="search"] {
    border-radius: $border-radius-xs 0 0 $border-radius-xs;
    border-right-width: 0;
    padding-right: $l-spacing-l;

    &:focus,
    &:hover {
      border-radius: $border-radius-s 0 0 $border-radius-s;
    }
  }

  .btn--plain {
    position: absolute;
    right: 115%;
    top: .5rem;
  }
}

.input-wrap--filled-white-search {
  &.input-wrap--labelled {
    input[type="search"] {
      padding-right: $l-spacing-2xl;
    }
  }

  .search-actions {
    position: absolute;
    right: $c-spacing-s;
    top: 50%;
    transform: translateY(-50%);
  }

  .btn--plain {
    right: $l-spacing-s;
  }
}

.input-wrap--labelled {
  .search-action-divider,
  .btn--plain {
    opacity: 1;
    pointer-events: initial;
  }

  .btn--plain {
    display: block;
  }
}

[dir="rtl"] {
  input[type="search"] ~ label {
    right: $c-spacing-m;
    left: auto;
  }

  .search-action-divider {
    left: $l-spacing-m;
    right: auto;
  }

  .input-wrap--filled-blue-search,
  .input-wrap--filled-white-search {
    label {
      top: $c-spacing-s;
    }
  }

  .input-wrap--filled-white-search {
    .search-actions {
      left: $c-spacing-s;
      right: initial;
    }

    .btn--plain {
      left: $l-spacing-s;
      right: initial;
    }

    &.input-wrap--labelled {
      input[type="search"] {
        padding-left: $l-spacing-2xl;
        padding-right: $c-spacing-m;
      }
    }
  }

  .input-wrap--filled-blue-search {
    .btn--plain {
      left: 115%;
      right: initial;
    }

    input[type="search"] {
      border-radius: 0 $border-radius-s $border-radius-s 0;
      border-right-width: $border-thickness-default;
      border-left-width: 0;
      padding-left: $l-spacing-l;
      padding-right: $c-spacing-m;
    }

    .btn--search {
      border-radius: $border-radius-xs 0 0 $border-radius-xs;

      &:focus:not(:disabled),
      &:hover:not(:disabled) {
        border-radius: $border-radius-s 0 0 $border-radius-s;
      }

      &:active:not(:disabled) {
        border-radius: $border-radius-s 0 0 $border-radius-s;
      }

    }
  }
}
