@import "./node_modules/@ikea-ux/variables/style.scss";

/**
 * Definition list
 *
 * Usage:
 *
 *  <dl>
 *    <dt>Name</dt>
 *    <dd>John Doe</dd>
 *    <dt>Age</dt>
 *    <dd>15</dd>
 *     <!-- ... -->
 *  </dl>
 *
 *  <dl class="dl-horizontal">
 *    <dt>Name</dt>
 *    <dd>John Doe</dd>
 *    <dt>Age</dt>
 *    <dd>15</dd>
 *    <!-- ... -->
 *  </dl>
 *
 *  <dl>
 *    <dt>Company name</dt>
 *    <dd>Some street 123/dd>
 *    <dd>123 123</dd>
 *    <dd>Country</dd>
 *    <!-- ... -->
 *  </dl>
 */

dl {
  margin-bottom: $c-spacing-xs;

  dd,
  dt {
    display: block;
  }

  dd {
    margin-bottom: $c-spacing-xs;
  }

  &.dl-horizontal {
    display: block;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;

    dd,
    dt {
      display: inline-block;
      padding-bottom: $c-spacing-xs;
      padding-top: $c-spacing-xs;
      border-bottom: $border-thickness-default solid $border-colour-idle;
      height: 100%;
    }

    dt {
      width: 10rem;
    }

    dd {
      width: 75%;
      width: calc(100% - 10rem);
      padding-left: 1rem;
      margin-bottom: 0;

      + dd {
        padding-left: 11rem;
        width: 100%;
      }
    }

    div {
      width: 100%;
      border-bottom: $border-thickness-default solid $border-colour-idle;
      display: flex;
      flex-wrap: wrap;
      margin-bottom: $c-spacing-xs;
      padding-bottom: $c-spacing-xs;
      align-items: center;

      dd,
      dt {
        border-bottom: 0;
        display: block;
        padding: 0;
      }

      dt {
        width: 10rem;
      }

      dd {
        width: 75%;
        width: calc(100% - 11rem);
        margin-bottom: 0;
      }
    }
  }
}
