@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/style.scss";
@import "./_mixins.scss";

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.js-focus-visible {
  input:focus.focus-visible {
    ~ .pill {
      @include focus();
    }
  }

  a,
  button,
  .btn,
  [role="button"],
  [data-popover],
  [data-tooltip] {
    &:focus.focus-visible {
      @include focus();
    }
  }

  a:focus.focus-visible {
    p {
      @include focus();
    }
  }

  input[type="file"]:focus.focus-visible {
    ~ .btn {
      @include focus();
    }
  }

  input[type="checkbox"]:focus.focus-visible {
    ~ .checkbox__symbol,
    ~ .switch__background {
      @include focus();
    }

    &:hover ~ .checkbox__symbol {
      @include focus($c-spacing-s);
    }
  }

  input[type="radio"]:focus.focus-visible {
    ~ .radio__symbol {
      @include focus();
    }

    &:hover ~ .radio__symbol {
      @include focus($c-spacing-s);
    }
  }

  &.tabs__panel:focus.focus-visible {
    @include focus();
  }
}
