@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/style.scss";
@import "./lib/_variables.scss";

.switch {
  position: relative;
  display: inline-block;
  display: inline-flex;
  align-items: center;

  &__background {
    position: relative;
    display: inline-block;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    align-items: center;
    width: 2.5rem;
    height: 1.5rem;
    cursor: pointer;
    border-radius: .75rem;
    background-color: $switch-toggle-background;
    transition: all .3s;
    align-self: flex-start;
  }

  &__ball {
    display: inline-block;
    width: 1rem;
    height: 1rem;
    border-radius: $border-radius-2xl;
    background-color: $switch-ball-background;
    transform: translateX(.25rem);
    align-items: center;
    transition: all $animation-fast;
  }

  &__icon {
    display: block;
    fill: $switch-icon-idle;
    width: 1rem;
    height: 1rem;
  }

  &__label {
    label {
      padding-left: $c-spacing-m;
      cursor: pointer;
    }
  }

  input[type="checkbox"] {
    top: 0;
    left: 0;
    right: 0;
    padding: 0;
    bottom: 0;
    border: 0;
    z-index: 1;
    cursor: pointer;
    opacity: 0;
  }

  // Checked
  input[type="checkbox"]:checked {
    ~ .switch__background {
      background: $switch-toggle-background-checked;
    }

    ~ .switch__background .switch__ball {
      transform: translateX(1.25rem);
    }

    ~ .switch__background .switch__icon {
      fill: $switch-icon-checked;
    }
  }

  // Hover
  input[type="checkbox"]:hover {
    ~ .switch__background {
      background: $switch-toggle-background-hover;
    }
  }

  input[type="checkbox"]:active {
    ~ .switch__background {
      background: $switch-toggle-background-pressed;
    }
  }

  // Checked hover
  input[type="checkbox"]:checked:hover {
    ~ .switch__background {
      background: $switch-toggle-background-checked-hover;
    }

    ~ .switch__background .switch__icon {
      fill: $switch-toggle-background-checked-hover;
    }
  }

  input[type="checkbox"]:focus:not(:disabled) {
    ~ .switch__focus {
      position: absolute;
      top: -$c-spacing-xs;
      left: -$c-spacing-xs;
      right: -$c-spacing-xs;
      bottom: -$c-spacing-xs;
      display: block;
      border: $border-thickness-focus solid $colour-feedback-focus;
      border-radius: $c-spacing-2xs;
    }
  }

  // Checked active
  input[type="checkbox"]:checked:active {
    ~ .switch__background {
      background: $switch-toggle-background-checked-pressed;
    }

    ~ .switch__background .switch__icon {
      fill: $switch-toggle-background-checked-pressed;
    }
  }

  // Disabled
  input[type="checkbox"]:disabled {
    ~ .switch__background {
      background: $switch-toggle-background-disabled;
    }

    ~ .switch__background .switch__icon {
      fill: $switch-icon-disabled;
    }

    ~ .switch__label label {
      cursor: initial;
    }
  }

  input[type="checkbox"]:checked:disabled {
    ~ .switch__background {
      background: $switch-toggle-background-checked-disabled;
    }

    ~ .switch__background .switch__icon {
      fill: $switch-icon-disabled-checked;
    }
  }
}
