@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/keyframes.scss";

.alert {
  position: relative;
  padding: $c-spacing-s $c-spacing-m;
  border-radius: $border-radius-xs;
  display: flex;
  flex-direction: row;

  .alert__container {
    display: inline-block;
    flex: 1 0;
    margin-left: $c-spacing-xs;
  }

  &__heading {
    margin-bottom: $c-spacing-xs;
  }

  &__text {
    margin-top: 0;
  }

  .alert__icon-style {
    line-height: 0;
  }

  &--s {
    padding: $c-spacing-xs;
    border-radius: $border-radius-xs;
    @include body-s();

    .alert__icon {
      margin-top: $c-spacing-2xs;
    }

    .alert__title {
      @include body-m();
      margin-bottom: 0;
    }
  }

  &--m {
    border-radius: $border-radius-s;

    .alert__title {
      @include body-m();
      margin-bottom: 0;
    }
  }

  &--l {
    padding: $c-spacing-m;
    border-radius: $border-radius-s;

    .alert__icon {
      position: absolute;
      left: 50%;
      top: $l-spacing-s;
      transform: translate(-50%, -50%);
    }

    .alert__text {
      margin-top: $l-spacing-m;
    }

    .alert__icon-style {
      display: inline-block;
      padding: $c-spacing-xs;
    }

    .alert__container {
      margin-left: 0;
      padding-top: 0;
      text-align: center;
    }
  }

  &--confirmation {
    background: adjustcolour($colour-feedback-confirmation, 80);
    border: $border-thickness-default adjustcolour($colour-feedback-confirmation, 80) solid;

    .alert__icon-style {
      // animation: jump both 1;
      animation-duration: 500ms;
    }
  }

  &--info {
    background: adjustcolour($colour-feedback-information, 80);
    border: $border-thickness-default adjustcolour($colour-feedback-information, 80) solid;

    .alert__icon-style {
      // animation: zoom-inn both 1;
      animation-duration: 500ms;
    }
  }

  &--warning {
    background: adjustcolour($colour-feedback-warning, 80);
    border: $border-thickness-default adjustcolour($colour-feedback-warning, 80) solid;

    .alert__icon-style {
      // animation: zoom-inn both 1;
      animation-duration: 500ms;
    }
  }

  &--error {
    background: $colour-feedback-error;
    border: $border-thickness-default $colour-feedback-error solid;
    color: $colour-neutral-white;

    .alert__icon-style {
      // animation: shake both 1;
      animation-duration: 700ms;
    }
  }

}

.alert__link:link .alert__link:active,
.alert__link:visited {
  text-decoration: none;
}

/**
  * Alert types
  */

.alert--error {
  border: $border-thickness-default solid $colour-feedback-error;
}

/* Loud style */

.alert--light {
  background: $colour-neutral-white;
  color: $colour-neutral-grey-900;
  border: $border-thickness-default solid $border-colour-idle;

  &.alert--l {
    &.alert--warning {
      border-color: $colour-feedback-warning;
    }

    &.alert--confirmation {
      border-color: $colour-feedback-confirmation;
    }

    &.alert--info {
      border-color: $colour-feedback-information;
    }

    &.alert--error {
      border-color: $colour-feedback-error;
    }
  }

  .alert__icon-style {
    &:before {
      border: $colour-feedback-error solid $border-thickness-default;
    }
  }
}
