@import "./node_modules/@ikea-ux/variables/style.scss";

.content-card {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  box-shadow: 0 0 2px 0 $colour-neutral-grey-300;
  border-radius: $border-radius-xs;
  overflow: hidden;
  transition: all $animation-normal $animation-timing;

  &:hover {
    border-radius: $border-radius-m;
    box-shadow: 0 0 2px 0 $border-colour-hover;

    img {
      transform: scale(1.025);
      transition: all $animation-slow $animation-timing;
    }
  }

  img {
    max-height: 25rem;
    display: block;
    margin: 0 auto;
    min-height: 15vw;
    transition: all $animation-slow $animation-timing;
  }

  .content-card__description a {
    text-decoration: none;

    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }
  }

  a.content-card__image-link {
    margin-bottom: 0;
    background: $colour-neutral-grey-100;
    width: 100%;
    overflow: hidden;

    &:hover,
    &:focus,
    &:active {
      + .content-card__description a {
        text-decoration: underline;
      }
    }
  }

  &__description {
    width: 100%;
    padding: $c-spacing-xs $c-spacing-xs $c-spacing-m $c-spacing-xs;
  }

  &__skeleton:empty {
    &:after {
      content: "";
      display: block;
      width: 100%;
      background-image: linear-gradient( 100deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, .5) 50%, rgba(255, 255, 255, 0) 80%), linear-gradient( $colour-neutral-grey-200 10rem, transparent 0), linear-gradient( $colour-neutral-grey-200 1.5rem, transparent 0), linear-gradient( $colour-neutral-grey-200 1.5rem, transparent 0), linear-gradient( $colour-neutral-grey-200 1rem, transparent 0), linear-gradient( $colour-neutral-grey-200 1rem, transparent 0);
      background-repeat: no-repeat;
      background-size: 250% 20rem, 100% 10rem, 80% 10rem, 60% 10rem, 70% 10rem, 70% 10rem;
      background-position: 100% 0, 0 0, 0 11rem, 0 13rem, 0 15.5rem, 0 17rem;
      animation: shine 2s infinite;
    }
    margin: auto;
    height: 22rem;
    max-width: 25rem;
    padding: $c-spacing-m;
  }
}

[class*="ikea-col-"] {
  .content-card {
    img {
      max-height: 17.5rem;
    }
  }
}

@keyframes shine {
  to {
    background-position: 0 100%, 0 0, 0 11rem, 0 13rem, 0 15.5rem, 0 17rem;
  }
}
