@import "./checkbox_variables.scss";

/**
* Custom checkboxes & checkbox buttons
*/
fieldset .checkbox {
  margin-bottom: $c-spacing-m;
  margin-right: $c-spacing-l;
}

input[type="checkbox"] {
  position: absolute;
  top: 0;
  left: -9999px;
  margin: 0;
  outline: 0;
}

.checkbox {
  position: relative;
  display: inline-block;

  input[type="checkbox"] {
    position: absolute;
    left: -$c-spacing-m;
    top: -$c-spacing-s;
    width: 3rem;
    height: 3rem;
    display: inline-block;
    border: 0;
    opacity: 0;
    padding: 0;
    z-index: 1;
    cursor: pointer;
  }

  ~ .inline-text {
    margin-top: $c-spacing-xs;
  }

  &__box {
    position: absolute;
    border-radius: $border-radius-xs;
    border: $border-thickness-default solid $checkbox-border-idle;
    width: 1rem;
    height: 1rem;
    display: block;
    background: $checkbox-background-idle;;
  }

  &__symbol {
    position: absolute;
    top: $c-spacing-2xs;
    left: 0;
    content: "";
    display: block;
    width: 1rem;
    height: 1rem;
    // border: $border-thickness-default solid $colour-brand-blue;
    transition: border $animation-fast, background $animation-fast, border-radius $animation-fast;
    // background: $checkbox-background-idle;

    & .checkbox__box-highlight {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      content: "";
      display: block;
      border-radius: $border-radius-s;
      width: 0px;
      height: 0px;
      transition: all $animation-normal;
    }

    .svg-icon {
      position: absolute;
      width: 0px;
      height: 0px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      fill: $checkbox-icon-idle;
    }
  }

  &__label {
    position: relative;
    display: block;
    padding-left: $c-spacing-xl;
  }

  label {
    cursor: pointer;
    display: inline;
    color: $colour-neutral-grey-900;
  }

  input[type="checkbox"]:checked {
    ~ .checkbox__symbol .checkbox__box {
      background: $checkbox-background-checked;
    }

    ~ .checkbox__symbol .svg-icon {
      opacity: 1;
      fill: $checkbox-icon-checked;
      width: .875rem;
      height: .875rem;
    }
  }

  input[type="checkbox"]:checked:hover {
    ~ .checkbox__symbol .checkbox__box {
      border-color: $checkbox-border-hover;
      background: $checkbox-background-checked-hover;
    }

    ~ .checkbox__symbol .svg-icon {
      fill: $checkbox-icon-checked;
    }

  }

  input[type="checkbox"]:hover {
    ~ .checkbox__symbol .checkbox__box {
      border-radius: $border-radius-xs;
      border-color: $checkbox-border-hover;
    }

    ~ .checkbox__symbol .checkbox__box-highlight {
      background: $checkbox-outer-highlight-hover;
      transition: all $animation-fast;
      width: 2rem;
      height: 2rem;
    }

    ~ .checkbox__symbol .svg-icon {
      opacity: 1;
      width: .875rem;
      height: .875rem;
      fill: $checkbox-icon-hover;
    }
  }

  input[type="checkbox"]:active:not(:disabled) {
    ~ .checkbox__symbol .checkbox__box {
      background: $checkbox-background-pressed;
      border-color: $checkbox-background-pressed;
    }

    ~ .checkbox__symbol .checkbox__box-highlight {
      background: $checkbox-outer-highlight-pressed;
    }

    ~ .checkbox__symbol .svg-icon {
      width: .875rem;
      height: .875rem;
      fill: $colour-neutral-white;
    }
  }

  input[type="checkbox"]:disabled {
    &:hover,
    &:checked {
      cursor: not-allowed;

      ~ .checkbox__symbol .checkbox__box-highlight {
        display: none;
      }

      ~ .checkbox__symbol {
        opacity: .4;
      }
    }

    &:not(:checked) {
      ~ .checkbox__symbol {
        opacity: .4;

        .svg-icon {
          display: none;
        }
      }
    }

    ~ .checkbox__label label {
      opacity: .4;
      cursor: not-allowed;
    }
  }
}

[dir="rtl"] {
  input[type="checkbox"] {
    left: auto;
    right: -9999px;
  }

  .checkbox {
    input[type="checkbox"] {
      right: 0;
      left: initial;
    }

    label {
      padding-right: $c-spacing-xl;
      padding-left: 0;
    }

    .checkbox__symbol {
      right: 0;
      left: initial;
    }
  }
}
