@import "./node_modules/@ikea-ux/variables/style.scss";

.breadcrumb {
  @include body-xs();

  a {
    font-weight: normal;
  }

  ol {
    padding-left: 0;
    list-style-type: none;
    display: inline-block;
    margin: 0;
    width: 100%;
    line-height: 2;

    li {
      padding-left: 0;
      margin-bottom: 0;
    }
  }

  a[role="button"].link--disabled {
    opacity: 1;
  }

  &--historical {
    display: flex;

    nav {
      display: inline-block;
      flex: 1 0;
    }

    li:nth-child(n+2) {
      display: none;
    }

    nav.breadcrumb--expanded {
      li:nth-child(n+2) {
        position: initial;
        height: initial;
        width: initial;
        display: inline-block;
      }

      .breadcrumb__toggle-nav-items {
        display: none;
      }

    }
  }

  &__last-visited {
    display: inline-block;
    margin-right: $c-spacing-m;
    padding-right: $c-spacing-m;
    border-right: $border-thickness-default solid $colour-neutral-grey-300;
    line-height: 2;

    .svg-icon {
      vertical-align: middle;
    }
  }

  &__item-separator {
    margin-left: $c-spacing-xs;
    margin-right: $c-spacing-xs;
  }

  button {
    -webkit-appearance: none;
    -moz-appearance: none;
    border: 0;
    padding: 0;
    background: none;
    color: inherit;
    max-width: 1rem;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    vertical-align: middle;
  }

  .breadcrumb__toggle-nav-items {
    display: inline-block;

    button {
      @include body-xs();
    }
  }

  nav.breadcrumb--expanded {
    button {
      display: none;
      width: auto;
      white-space: initial;
      overflow: initial;
      text-overflow: initial;
      vertical-align: initial;
      max-width: initial;
    }

    li.breadcrumb__toggle-nav-items {
      display: none;
    }

    li:nth-child(n+3) {
      position: initial;
      height: initial;
      width: initial;
      display: inline-block;
    }

    li.active {
      display: inline-block;
    }
  }

  li {
    display: inline-block;
    max-width: 100%;
    margin-bottom: $c-spacing-2xs;
  }

  li:nth-child(n+3) {
    display: none;
  }

  li:nth-last-child(2) {
    display: inline-block;
  }

  li.active {
    display: inline-block;

    a {
      cursor: default;
      text-decoration: none;
      text-align: left;
      font-weight: bold;
    }
  }
}

@media screen and (min-width: $breakpoints-medium) {
  .breadcrumb {
    li:nth-child(n+3) {
      position: initial;
      height: initial;
      width: initial;
      display: inline-block;
    }

    li.breadcrumb__toggle-nav-items {
      display: none;
    }
  }
}
