@mixin ikeacontainer() {
  display: block;
  margin: auto;
  padding: $grid-margin-xs;
  max-width: $grid-max-width;

  @media all and (min-width: $breakpoints-small) {
    padding: $grid-margin-s;
  }

  @media all and (min-width: $breakpoints-large) {
    padding: $grid-margin-l;
  }

  @media all and (min-width: $breakpoints-2xlarge) {
    padding: $grid-margin-2xl;
  }
}

@mixin ikearow() {
  display: block;
  display: flex;
  flex-wrap: wrap;
}

@mixin ikeanestedrow() {
  margin: #{-$c-spacing-2xs/2};

  [class*="ikea-col"] {
    margin-bottom: 0;
  }

  @media all and (min-width: $breakpoints-small) {
    margin: #{-$grid-gutter-s};
  }

  @media all and (min-width: $breakpoints-large) {
    margin: #{-$grid-gutter-l};
  }

  @media all and (min-width: $breakpoints-2xlarge) {
    margin: #{-$grid-gutter-2xl};
  }
}

// Styles for automatic columns
@mixin ikeaautocol() {
  display: inline-block;
  min-width: 100%;
  flex-grow: 1;
  flex-basis: 0;
  padding: $c-spacing-2xs/2;

  @media all and (min-width: $breakpoints-small) {
    min-width: 8.33%;
    padding: $grid-gutter-s;
  }

  @media all and (min-width: $breakpoints-large) {
    padding: $grid-gutter-l;
  }

  @media all and (min-width: $breakpoints-2xlarge) {
    padding: $grid-gutter-2xl;
  }
}

/* First parameter define amount of columns (default: 1)
 * Second parameter define how many columns each item should span (default: 1)
 * Third parameter defines if the column is fluid or not
 */

// Styles for columns with defined size.
@mixin ikeagetcolDefault($colcount: 1, $cols: 1) {
  // If $colcount is not an integer and not within range
  @if (not(isint($colcount) and $colcount >= 1 and $colcount <= 12)) {
    $colcount: 1;
  }

  // If $cols is not an integer and not within range
  @if (not(isint($cols) and $cols >= 1 and $cols <= 12)) {
    $cols: 1;
  }
  position: relative;
  width: 100%;
  min-height: 1px;
  padding: 0;
}

@mixin ikeagetcol($colcount: 1, $cols: 1, $breakpoint: "s") {
  $padding: $grid-gutter-s;

  @if ($breakpoint == "m") {
    $padding: $grid-gutter-m;
  } @else if ($breakpoint == "l") {
    $padding: $grid-gutter-l;
  } @else if ($breakpoint == "xl") {
    $padding: $grid-gutter-xl;
  } @else if ($breakpoint == "2xl") {
    $padding: $grid-gutter-2xl;
  }

  // If $colcount is not an integer and not within range
  @if (not(isint($colcount) and $colcount >= 1 and $colcount <= 12)) {
    $colcount: 1;
  }

  // If $cols is not an integer and not within range
  @if (not(isint($cols) and $cols >= 1 and $cols <= 12)) {
    $cols: 1;
  }
  $base: 100% / $colcount;
  $width: $base * $cols;

  display: inline-block;
  padding: $padding;
  flex: 0 0 #{$width};
  max-width: #{$width};
}

@mixin ikeaoffset($colcount: 1, $offsetby: 1) {
  $base: 100% / $colcount;
  $width: $base * $offsetby;

  margin-left: $width;
}

@mixin ikeaoffsetRTL($colcount: 1, $offsetby: 1) {
  $base: 100% / $colcount;
  $width: $base * $offsetby;

  margin-right: $width;
  margin-left: auto;
}
