.definition-list {
  display: table;

  thead {
    display: table-header-group;

    th {
      padding-right: $l-spacing-s;
    }
  }

  th {
    background: none;
    color: $colour-neutral-grey-900;
  }

  tbody tr:not(:last-child) {
    border-bottom: none;
  }

  &--glossary {
    display: block;

    tbody,
    th,
    td,
    tr {
      display: block;
    }

    td,
    th {
      width: 100%;
    }

    thead {
      display: block;
    }

    thead th {
      // padding-top: 0;
      padding-bottom: $l-spacing-2xs;
    }

    tbody {
      tr:first-child {
        th {
          padding-top: $l-spacing-2xs;
        }
      }

      th {
        padding-top: $c-spacing-xs;
        padding-bottom: 0;
      }

      td {
        padding-top: 0;
        padding-bottom: $c-spacing-xs;
      }
    }
  }

  &--numerical {
    thead th {
      padding-top: 0;
      padding-bottom: $l-spacing-2xs;
    }

    tbody {
      display: table-row-group;

      tr:first-child {
        th,
        td {
          padding-top: $l-spacing-2xs;
        }
      }
    }

    tr {
      display: table-row;
    }

    th,
    td {
      display: table-cell;
      width: 66.67%;
    }
  }
}

@media screen and (min-width: $breakpoints-medium) {
  .definition-list {
    thead th {
      padding-right: 0;
    }

    &--numerical {
      th {
        width: 20%;
        min-width: 9rem;
      }

      th,
      td {
        padding-left: 0;
        padding-top: $c-spacing-xs;
        padding-bottom: $c-spacing-xs;
      }
    }

    &--glossary {
      display: table;

      thead {
        display: table-header-group;
      }

      tbody {
        display: table-row-group;

        tr:first-child {
          th,
          td {
            padding-top: $l-spacing-2xs;
          }
        }

        td {
          padding-top: $c-spacing-xs;
          padding-bottom: $c-spacing-xs;
        }

        th {
          width: 33%;
          min-width: 9rem;
        }
      }

      tr {
        display: table-row;
      }

      td,
      th {
        display: table-cell;
        padding-left: 0;
        padding-right: 0;
      }
    }
  }
}
