@mixin btn-outlined(
  $bg-default: $colour-neutral-grey-900,
  $bg-hover: $colour-neutral-grey-900,
  $bg-pressed: $colour-neutral-grey-800,
  $text-default: $colour-neutral-grey-900,
  $text-hover: $colour-neutral-white,
  $text-pressed: $colour-neutral-white
) {
  @include btn-outlined-idle($bg-default, $text-default);

  &:focus:not(:disabled),
  &:hover:not(:disabled) {
    @include btn-outlined-hover($bg-hover, $text-hover);
  }

  &:active:not(:disabled) {
    @include btn-outlined-pressed($bg-hover, $text-hover);
  }
}

@mixin btn-outlined-idle(
  $bg-default: $colour-neutral-grey-900,
  $text-default: $colour-neutral-grey-900
) {
  @include btn-state(
    transparent,
    $text-default,
    $border-radius-xs,
    #{$c-spacing-s - $border-thickness-double-rem} $c-spacing-l,
    $border-thickness-double solid $bg-default
  );
}

@mixin btn-outlined-hover(
  $bg-hover: $colour-neutral-grey-900,
  $text-hover: $colour-neutral-white
) {
  @include btn-state($bg-hover, $text-hover, $border-radius-s);

  &.btn--loading {
    background: transparent;
  }
}

@mixin btn-outlined-pressed(
  $bg-pressed: $colour-neutral-grey-800,
  $text-pressed: $colour-neutral-white
) {
  @include btn-state($bg-pressed, $text-pressed, $border-radius-l);

  &.btn--loading {
    background: transparent;
  }
}
