.input-wrap,
.input-wrap--labelled {
  position: relative;
  display: block;
  padding: 0;

  > .svg-icon,
  > .btn--plain {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
}

.input-wrap {
  label {
    position: absolute;
    left: calc(#{$c-spacing-m} + #{$border-thickness-default});
    top: $c-spacing-m + $c-spacing-2xs;
  }
}

.input-wrap--labelled {
  label {
    position: absolute;
    top: $c-spacing-m;
    left: calc(#{$c-spacing-m} + #{$border-thickness-default});
    font-size: $body-font-size-xs;
  }
}

.input-wrap textarea ~ label {
  top: $border-thickness-default;
  right: $border-thickness-default;
  left: $border-thickness-default;
  padding: #{$c-spacing-m + $c-spacing-2xs - $border-thickness-default-rem} $c-spacing-m 0 $c-spacing-m;
}

.input-wrap--labelled textarea ~ label {
  background: $colour-neutral-white;
  border-radius: $border-radius-m $border-radius-m 0 0;
  padding: #{$c-spacing-m - $border-thickness-default-rem} $c-spacing-m 0 $c-spacing-m;
  font-size: $body-font-size-xs;
}

// Input wrapper
// Used for positioning SVG icons and labelling form fields
.input-wrap--trailing-icon {
  select {
    padding-right: $c-spacing-2xl + $c-spacing-m;
  }

  input[type="text"],
  input[type="email"],
  input[type="password"],
  input[type="tel"],
  input[type="url"] {
    padding-right: $l-spacing-l + $c-spacing-m;
  }

  > .svg-icon {
    right: $c-spacing-m;
  }

  > .btn--plain {
    right: $c-spacing-2xs;
  }
}

.input-wrap--leading-icon {
  > .svg-icon {
    left: $c-spacing-m;
  }

  > .btn--plain {
    left: $c-spacing-2xs;
  }

  label,
  &.input-wrap--labelled label {
    left: $c-spacing-2xl + $c-spacing-m;
  }

  select,
  input:not([type="range"]) {
    padding-left: $c-spacing-2xl + $c-spacing-m;
  }
}

[dir="rtl"] {
  .input-wrap {
    input[type="text"] ~ label,
    input[type="email"] ~ label,
    input[type="password"] ~ label,
    input[type="tel"] ~ label,
    input[type="url"] ~ label {
      right: $c-spacing-m;
      left: auto;
    }
  }

  .input-wrap.input-wrap--leading-icon {
    select,
    input:not([type="range"]) {
      padding-right: $c-spacing-2xl + $c-spacing-m;
      padding-left: $c-spacing-m;

      ~ label {
        right: $c-spacing-2xl + $c-spacing-m;
        left: initial;
      }
    }

    > .svg-icon {
      left: initial;
      right: $c-spacing-m;
    }

    > .btn--plain {
      left: initial;
      right: $c-spacing-2xs;
    }
  }

  .input-wrap.input-wrap--trailing-icon {
    select {
      padding-left: $c-spacing-2xl + $c-spacing-m;
      padding-right: $c-spacing-m;
    }

    input[type="text"],
    input[type="email"],
    input[type="password"],
    input[type="tel"],
    input[type="url"] {
      padding-left: $l-spacing-l + $c-spacing-m;
      padding-right: $c-spacing-m;
    }

    > .svg-icon {
      left: $c-spacing-m;
      right: initial;
    }

    > .btn--plain {
      left: $c-spacing-2xs;
      right: initial;
    }
  }
}
