@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/keyframes.scss";

@include zoom-leave(lb-zoom-leave);
@include zoom-enter(lb-zoom-enter);

.lightbox {
  z-index: 0;

  &--open {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10000;
    text-align: center;
  }

  &--dark {
    background: rgba(0, 0, 0, .8);
  }

  &--light {
    background: rgba($colour-neutral-white, .8);
  }

  .zoom-enter {
    animation: lb-zoom-enter both $animation-timing;
    animation-duration: $animation-normal;
    display: block;
    display: flex;
    flex-direction: column;

    ~ *:not(.modal) {
      display: none;
    }
  }

  .zoom-leave {
    display: none;
    animation: lb-zoom-leave both;
    width: 0;
    height: 0;
    padding: 0;
    animation-duration: $animation-normal;
  }
}

@media screen and (min-width: $breakpoints-medium) {
  .lightbox {
    .zoom-enter {
      position: relative;
      min-width: 25rem;
      min-height: 4rem;
      max-width: 80%;
      max-height: 90%;
      width: auto;
      display: inline-block;
      display: flex;
      margin: $l-spacing-m auto;
      text-align: left;

      &.modal__video {
        max-width: 50%;
      }
    }
  }
}
