@import "./node_modules/@ikea-ux/variables/style.scss";

.product-identifier {
  display: inline-block;
  font-size: $body-font-size-xs;
  line-height: 2;
  font-weight: bold;

  &__prefix {
    display: inline-block;
    background: $colour-neutral-grey-100;
    color: $colour-neutral-grey-900;
    padding: 0 $c-spacing-xs;
  }

  &__number {
    display: inline-block;
    background: $colour-neutral-grey-900;
    color: $colour-neutral-white;
    padding: 0 $c-spacing-m;
  }

  &--cabinet-code {
    .product-identifier__number {
      padding: 0 $c-spacing-xs;
    }
  }

  + .product-identifier {
    margin-left: $c-spacing-m;
  }
}
