@import "./node_modules/@ikea-ux/animations/style.scss";
@import "./node_modules/@ikea-ux/variables/style.scss";

@include rotate(icon-rotate);

/**
 * General inline SVG icon styling
 */
[class*="white"] {
  fill: $colour-neutral-white;
}

.leading-icon {
  .svg-icon {
    margin-right: $c-spacing-xs;
    vertical-align: top;
  }

  .svg-icon--100 {
    margin-right: $c-spacing-2xs;
  }
}

.trailing-icon {
  .svg-icon {
    margin-left: $c-spacing-xs;
    vertical-align: top;
  }

  .svg-icon--100 {
    margin-left: $c-spacing-2xs;
  }
}

/* Default icon size */

.svg-icon {
  display: inline-block;
  height: 1.5rem;
  width: 1.5rem;
  vertical-align: middle;
  fill: $colour-neutral-grey-900;

  .white,
  [class*="white"],
  g.white,
  g[class*="white"],
  path.white,
  path[class*="white"] {
    fill: $colour-neutral-white;
  }

  @include transition(transform fill);

  &--100 {
    width: 1rem;
    height: 1rem;
  }

  &--150 {
    height: 1.5rem;
    width: 1.5rem;
  }

  &--200 {
    height: 2rem;
    width: 2rem;
  }

  &--250 {
    height: 2.5rem;
    width: 2.5rem;
  }

  &--300 {
    height: 3rem;
    width: 3rem;
  }

  &--rotate {
    animation: icon-rotate 1.5s linear infinite;
  }

  &--rotate-45 {
    transform: rotate(45deg);
  }

  &--rotate-minus-45 {
    transform: rotate(-45deg);
  }

  &--rotate-180 {
    transform: rotate(180deg);
  }

  &--rotate-minus-180 {
    transform: rotate(-180deg);
  }

  &.black {
    fill: $colour-neutral-grey-900;
  }

  &.grey {
    fill: $colour-neutral-grey-700;
  }
}

a {
  .svg-icon {
    fill: $colour-blue-idle;
  }

  &:hover,
  &:focus {
    .svg-icon {
      fill: $colour-blue-hover;
    }
  }

  &:active {
    .svg-icon {
      fill: $colour-blue-pressed;
    }
  }

  &:hover,
  &:active,
  &:focus {
    .svg-icon.black,
    .svg-icon.grey {
      fill: $colour-neutral-grey-800;
    }
  }
}
