@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/keyframes.scss";

@include fade-out-in(toast-fade-out-in);
@include fade-in-up(toast-fade-in-up);

.toast-container {
  position: fixed;
  bottom: $c-spacing-m;
  right: auto;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  width: 90%;
  z-index: 8000;
}

.toast {
  position: relative;
  display: none;
  padding: $c-spacing-m $c-spacing-l $c-spacing-m $c-spacing-m;
  background: $colour-neutral-white;
  width: 100%;
  margin-bottom: $c-spacing-xs;
  border: $border-thickness-default solid $border-colour-idle;
  border-radius: $border-radius-s;
  opacity: 0;
  animation: toast-fade-out-in both $animation-timing;
  animation-duration: 300ms;

  &__show {
    display: flex;
    opacity: 1;
    animation: toast-fade-in-up both $animation-timing;
    animation-duration: 700ms;
  }

  &__close {
    position: absolute;
    top: 0;
    right: 0;
    margin-bottom: 0;
    color: $colour-neutral-grey-700;
  }

  &__icon {
    border-radius: $border-radius-2xl;
    display: inline-block;
    vertical-align: top;
    align-self: flex-start;
    margin-right: $c-spacing-m;
  }

  &__body {
    display: inline-block;
  }

  &__title {
    font-weight: bold;
  }

  &__text {
    color: $colour-neutral-grey-900;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

@media screen and (min-width: $breakpoints-medium) {
  .toast-container {
    width: 28.75em;
    right: $c-spacing-xl;
    left: auto;
    transform: translateX(0);
  }
}
