@import "./node_modules/@ikea-ux/variables/style.scss";
@import "./node_modules/@ikea-ux/animations/style.scss";

.badge {
  position: absolute;
  top: -$c-spacing-2xs;
  transform: translateX(-$c-spacing-xs);
  background: $colour-brand-yellow;
  padding: 0 $c-spacing-2xs;
  border-radius: $border-radius-l;
  font-weight: bold;
  font-size: $body-font-size-xs;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 5rem;
  overflow: hidden;
  left: 100%;
  line-height: 1.333;

  &--red {
    background: $colour-feedback-error;
    color: $colour-neutral-white;
  }

  &--yellow {
    background: $colour-brand-yellow;
  }

  &--blue {
    background: $colour-brand-blue;
  }

  &--black {
    background: $colour-neutral-grey-900;
    color: $colour-neutral-white;
  }
}
